.user-nav {
  display: flex;
  align-items: center;
  align-self: stretch; }
  .user-nav > * {
    display: flex;
    align-items: center;
    padding: 0 2rem;
    cursor: pointer;
    height: 100%; }
  .user-nav > *:hover {
