  align-items: center;
  font-size: 1.3rem;
  color: var(--color-grey-dark-3); }
  .recommend__count {
    margin-right: auto; }
  .recommend__friends {
    display: flex; }
  .recommend__photo {
    box-sizing: content-box;
    height: 4rem;
    width: 4rem;
    border-radius: 50%;
    border: 3px solid #fff; }
    .recommend__photo:not(:last-child) {
      margin-right: -2rem; }

.review {
  background-color: #fff;
  box-shadow: var(--shadow-light);
  padding: 3rem;
  margin-bottom: 3.5rem;
  position: relative;
  overflow: hidden; }
  @media only screen and (max-width: 56.25em) {
    .review {
      padding: 2rem;
      margin-bottom: 3rem; } }
  .review::before {
    position: absolute;
    content: "\201C";
    color: var(--color-grey-light-2);
    top: -2.75rem;
    left: -1rem;
    line-height: 1;
    font-family: sans-serif;
    font-size: 20rem;
    z-index: 1; }
  .review__text {
    position: relative;
    margin-bottom: 2rem;
    z-index: 2; }
  .review__user {
    display: flex;
    align-items: center; }
  .review__photo {
    height: 4.5rem;
    width: 4.5rem;
    border-radius: 50%;
    margin-right: 1.5rem; }
  .review__user-box {
    margin-right: auto; }
  .review__user-name {
    font-size: 1.1rem;
    font-weight: 600;
    text-transform: uppercase;
    margin-right: 0.4rem; }
  .review__user-date {
