//////////////////////////////
// LOGO
.logo {
	height: 3.25rem;
	margin-left: 2rem;
}

/////////////////////////////
// SEARCH
.search {
	flex: 0 0 40%;
	display: flex;
	justify-content: center;
	align-items: center;

	@media only screen and (max-width: $bp-smallest) {
		order: 1;
		flex: 0 0 100%;
		background-color: var(--color-grey-light-1);
	}

	&__button {
		border: none;
		background-color: var(--color-grey-light-1);
		cursor: pointer;

		&:focus {
			outline: none;
		}

		&:active {
			transform: translateY(2px);
		}
	}

	&__input {
		width: 90%;
		font-family: inherit;
		font-size: inherit;
		background-color: var(--color-grey-light-1);
		color: inherit;
		padding: 0.7rem 2rem;
		border-radius: 100px;
		border: none;
		transition: all 0.2s;
		margin-right: -3.25rem;

		@media only screen and (max-width: $bp-smallest) {
			margin-right: 0;
		}

		&:focus {
			outline: none;
			width: 100%;
			background-color: var(--color-grey-light-2);

			&::-webkit-input-placeholder {
				font-weight: 100;
				color: var(--color-grey-light-4);
			}

			& + .search__button {
				background-color: var(--color-grey-light-2);
			}
		}
	}

	&__icon {
		height: 2rem;
		width: 2rem;
		fill: var(--color-grey-dark-3);

		@media only screen and (max-width: $bp-smallest) {
			margin-left: -4rem;
		}
	}
}

/////////////////////////////
// USER NAVIGATION
.user-nav {
	display: flex;
	align-items: center;
	align-self: stretch;

	& > * {
		display: flex;
		align-items: center;
		padding: 0 2rem;
		cursor: pointer;
		height: 100%;
	}

	& > *:hover {
		background-color: var(--color-grey-light-1);
	}

	&__icon-box {
		position: relative;
	}

	&__icon {
		height: 2.25rem;
		width: 2.25rem;
		fill: var(--color-grey-dark-2);
	}

	&__notification {
		width: 1.75rem;
		height: 1.75rem;
		font-size: 0.8rem;
		border-radius: 50%;
		background-color: var(--color-pink);
		color: #fff;
		position: absolute;
		top: 1.5rem;
		right: 1.1rem;
		display: flex;
		justify-content: center;
		align-items: center;
	}

	&__user-photo {
		height: 3.75rem;
		border-radius: 50%;
		margin-right: 1rem;
	}
}

/////////////////////////////
// SIDE NAVIGATION
.side-nav {
	font-size: 1.4rem;
	list-style: none;
	margin-top: 3.5rem;

	@media only screen and (max-width: $bp-medium) {
		display: flex;
		margin: 0;
	}

	&__item {
		position: relative;
		cursor: pointer;

		&:not(:last-child) {
			margin-bottom: 0.5rem;

			@media only screen and (max-width: $bp-medium) {
				margin: 0;
			}
		}

		@media only screen and (max-width: $bp-medium) {
			flex: 1;
		}

		&::before {
			content: "";
			position: absolute;
			top: 0;
			left: 0;
			height: 100%;
			width: 3px;
			background-color: var(--color-primary);
			transform: scaleY(0);
			transition:
				transform 0.2s,
				width 0.4s cubic-bezier(1, 0, 0, 1) 0.2s,
				background-color 0.1s;
		}

		&--active::before,
		&:hover::before {
			transform: scaleY(1);
			width: 100%;
		}

		&:active::before {
			background-color: var(--color-primary-light);
		}
	}

	&__link:link,
	&__link:visited {
		display: flex;
		align-items: center;
		flex-wrap: nowrap;
		padding: 1.5rem 3rem;
		color: var(--color-grey-light-1);
		text-decoration: none;
		text-transform: uppercase;
		position: relative;
		z-index: 10;

		@media only screen and (max-width: $bp-medium) {
			justify-content: center;
			padding: 2rem;
		}

		@media only screen and (max-width: $bp-small) {
			flex-direction: column;
			padding: 1.5rem 0.5rem;
		}
	}

	&__link:hover {
		color: var(--color-grey-dark-3);
	}

	&__item--active &__link:link,
	&__item--active &__link:visited {
		color: var(--color-grey-dark-3);
	}

	&__icon {
		width: 1.75rem;
		height: 1.75rem;
		margin-right: 2rem;
		fill: currentColor;

		@media only screen and (max-width: $bp-small) {
			margin-right: 0;
			margin-bottom: 0.7rem;
			width: 1.5rem;
			height: 1.5rem;
		}
	}
}

/////////////////////////////
// LEGAL TEXT
.legal {
	padding: 2.5rem;
	text-align: center;
	color: var(--color-grey-light-4);
	font-size: 1.2rem;

	@media only screen and (max-width: $bp-medium) {
		display: none;
	}
}

/////////////////////////////
// GALLERY
.gallery {
	display: flex;

	&__photo {
		width: 100%;
		display: block;
	}
}

/////////////////////////////
// HOTEL OVERVIEW
.overview {
	display: flex;
	align-items: center;
	border-bottom: var(--line);

	@media only screen and (max-width: $bp-xxsmall) {
		justify-content: space-between;
		flex-wrap: wrap;
		padding-bottom: 1.5rem;
	}

	&__item {
		@media only screen and (max-width: $bp-xxsmall) {
			padding: 1.25rem 2rem;
		}
	}

	&__heading {
		font-size: 2.25rem;
		font-weight: 300;
		text-transform: uppercase;
		letter-spacing: 1px;
		padding: 1.5rem 3rem;

		@media only screen and (max-width: $bp-small) {
			font-size: 1.8rem;
			padding: 1.25rem 2rem;
		}
	}

	&__stars {
		display: flex;
		margin-right: auto;

		@media only screen and (max-width: $bp-xxsmall) {
			margin-right: 0;
			order: 1;
		}
	}

	&__icon-star,
	&__icon-location {
		width: 1.75rem;
		height: 1.75rem;
		fill: var(--color-pink);
	}

	&__location {
		display: flex;
		align-items: center;
		font-size: 1.2rem;
	}

	&__icon-location {
		margin-right: 0.5rem;
	}

	&__rating {
		display: flex;
		align-items: center;
		justify-content: center;
		flex-direction: column;
		margin-bottom: -3px;
		background-color: var(--color-pink);
		margin-left: 3rem;
		color: #fff;
		padding: 0 2.25rem;
		align-self: stretch;

		@media only screen and (max-width: $bp-small) {
			padding: 0 1.5rem;
		}
	}

	&__rating-average {
		font-size: 2.25rem;
		font-weight: 300;

		@media only screen and (max-width: $bp-small) {
			font-size: 1.8rem;
		}
	}

	&__rating-acount {
		font-size: 0.8rem;
		text-transform: uppercase;

		@media only screen and (max-width: $bp-small) {
			font-size: 0.5rem;
		}
	}
}

/////////////////////////////
// BUTTON INLINE
.btn-inline {
	display: inline-block;
	padding-bottom: 0.2rem;
	background-color: transparent;
	border: none;
	color: var(--color-pink);
	font-size: inherit;
	border-bottom: 1px solid currentColor;
	cursor: pointer;
	transition: all 0.2s;

	& span {
		margin-left: 3px;
		transition: margin-left 0.2s;
	}

	&:hover {
		color: var(--color-grey-dark-1);

		& span {
			margin-left: 8px;
		}
	}

	&:focus {
		outline: none;
		animation: pulsate 1s infinite;
	}
}

@keyframes pulsate {
	0% {
		transform: scale(1);
		box-shadow: none;
	}

	50% {
		transform: scale(1.05);
		box-shadow: 0 1rem 4rem rgba(0, 0, 0, 0.25);
	}
}

/////////////////////////////
// PARAGRAPH
.paragraph:not(:last-of-type) {
	margin-bottom: 2rem;
}

/////////////////////////////
// LIST
.list {
	display: flex;
	flex-wrap: wrap;
	margin: 3rem 0;
	padding: 3rem 0;
	border-top: var(--line);
	border-bottom: var(--line);
	list-style: none;

	&__item {
		flex: 0 0 50%;
		margin-bottom: 0.7rem;

		&::before {
			content: "";
			display: inline-block;
			width: 1rem;
			height: 1rem;
			margin-right: 0.7rem;
			//background-image: url("../img/chevron-thin-right.svg");
			//background-size: cover;

			background-color: var(--color-pink);
			-webkit-mask-image: url("../img/chevron-thin-right.svg");
			-webkit-mask-size: cover;
			mask-image: url("../img/chevron-thin-right.svg");
			mask-size: cover;
		}
	}
}

/////////////////////////////
// RECOMMEND
.recommend {
	display: flex;
	align-items: center;
	font-size: 1.3rem;
	color: var(--color-grey-dark-3);

	&__count {
		margin-right: auto;
	}

	&__friends {
		display: flex;
	}

	&__photo {
		box-sizing: content-box;
		height: 4rem;
		width: 4rem;
		border-radius: 50%;
		border: 3px solid #fff;

		&:not(:last-child) {
			margin-right: -2rem;
		}
	}
}

/////////////////////////////
// REVIEWS
.review {
	background-color: #fff;
	box-shadow: var(--shadow-light);
	padding: 3rem;
	margin-bottom: 3.5rem;
	position: relative;
	overflow: hidden;

	@media only screen and (max-width: $bp-medium) {
		padding: 2rem;
		margin-bottom: 3rem;
	}

	&::before {
		position: absolute;
		content: "\201C";
		color: var(--color-grey-light-2);
		top: -2.75rem;
		left: -1rem;
		line-height: 1;
		font-family: sans-serif;
		font-size: 20rem;
		z-index: 1;
	}

	&__text {
		position: relative;
		margin-bottom: 2rem;
		z-index: 2;
	}

	&__user {
		display: flex;
		align-items: center;
	}

	&__photo {
		height: 4.5rem;
		width: 4.5rem;
		border-radius: 50%;
		margin-right: 1.5rem;
	}

	&__user-box {
		margin-right: auto;
	}

	&__user-name {
		font-size: 1.1rem;
		font-weight: 600;
		text-transform: uppercase;
		margin-right: 0.4rem;
	}

	&__user-date {
		color: var(--color-grey-dark-3);
		font-size: 1rem;
	}

	&__rating {
		color: var(--color-pink);
		font-size: 2.2rem;
		font-weight: 300;
	}
}

/////////////////////////////
// CTA
.cta {
	padding: 3.5rem 0;
	text-align: center;

	@media only screen and (max-width: $bp-medium) {
		padding: 2.5rem 0;
	}

	&__book-now {
		text-transform: uppercase;
		font-size: 1.5rem;
		font-weight: 300;
		margin-bottom: 2.5rem;
		padding: 0 2rem;
	}
}

/////////////////////////////
// CTA BUTTON
.btn {
	font-size: 1.5rem;
	border: none;
	font-weight: 400;
	text-transform: uppercase;
	border-radius: 100px;
	background-image: linear-gradient(to right, var(--color-pink-light), var(--color-pink-dark));
	color: #ffffff;
	position: relative;
	overflow: hidden;
	cursor: pointer;

	& > * {
		display: inline-block;
		width: 100%;
		height: 100%;
		transition: all 0.2s;
	}

	&__visible {
		padding: 2rem 7.5rem;
	}

	&__invisible {
		position: absolute;
		left: 0;
		top: -100%;
		padding: 2rem 0;
	}

	&:hover {
		background-image: linear-gradient(to left, var(--color-pink-light), var(--color-pink-dark));
	}

	&:hover &__visible {
		transform: translateY(100%);
	}

	&:hover &__invisible {
		top: 0;
	}

	&:focus {
		outline: none;
		animation: pulsate 1s infinite;
	}
}
